@import url(https://fonts.googleapis.com/css2?family=Archivo:wght@300&family=Inter:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: linear-gradient(360deg, #e7dada, #d2ced9cc);
}

a {
  text-decoration: none !important;
}

p {
  font-family: 'Inter', sans-serif;
  font-size: 17px;
  font-weight: 100;
}

h1, h2, h3, h4, h5 {
  font-family: 'Archivo', sans-serif;
}

li {
  list-style: none;
}

ul {
  margin: 0;
  padding: 0;
}

.app {
  flex-direction: column;
  align-items: center;
  padding: 0;
}

.filtersTab {
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  color: white;
  padding: 1rem;
  align-items: center;
  background-color: #0D6ED7;
  -webkit-clip-path: polygon(50% 100%, 0 0, 100% 0);
          clip-path: polygon(50% 100%, 0 0, 100% 0);
  border-top: 2px solid #0d6e9d;
  height: 150px;
}

.eventListAll {
  width: 40%;
  text-align: center;
  margin-top: 3rem;
}

.EventList {
  padding: 0;
}

.event {
  margin-bottom: 40px;
  box-shadow: 10px 12px 36px;
}

.event-description {
  padding: 20px;
}

.hide-details {
  background-color: #f92020 !important;
  border-color: #f92020 !important;
}

.overlayInfo {
  background: #000000c2;
  width: 100%;
  position: fixed;
  left: 0;
  height: 100%;
  color: white;
  z-index: 3;
  display: flex;
}

.hideOverlayInfo {
  display: none;
}

.overlayHeader {
  transition-duration: .5s;
  width: 50%;
  text-align: center;
  position: absolute;
  right: 25%;
  bottom: 40%;
}

.overlayCloseButton {
  position: absolute;
  right: 10%;
  bottom: 94%;
}

.onExitButton {
  cursor: pointer;
  color: red;
}

.onExitButton:hover {
  color: white;
}

.onExitButton:active {
  color: blue;
}

.footerbar {
  background-color: #0070e9;
  width: 100%;
  height: 100px;
  text-align: center;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-clip-path: polygon(50% 0, 100% 40%, 100% 100%, 0 100%, 0 40%);
          clip-path: polygon(50% 0, 100% 40%, 100% 100%, 0 100%, 0 40%);
}

.header {
  width: 100%;
}

.navbarHeaderText {
  font-size: 16px;
}

#home {
  color: white !important;
}

.Alert {
  font-size: 16px;
}

@media only screen and (max-width: 992px) {

  .eventListAll {
    width: 100%;
    z-index: 0;
  }
  
  .EventList {
    position: relative;
    top: 20%;
  }

  .overlayHeader {
    width: 95%;
    text-align: center;
    position: absolute;
    right: 2%;
    bottom: 30%;
  }
}
/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;
  transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;
  animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}
@keyframes nprogress-spinner {
  0%   { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.CitySearch {
  padding: 20px;
}

.city {
  width: 65%;
  display: inline;
}

.suggestionList {
  transition-duration: .5s;
}

.suggestionList:hover {
  opacity: .5;
  cursor: pointer;
  transform: scale(1.1);
}

.suggestion {
  transition-duration: 1s;
}
.num-of-events {
  width: 65%;
  display: inline;
}
